* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  background-color: #ff6600;
  font-family: "Libre Franklin", sans-serif;
}

.app-container {
  display: flex;
  flex-direction: row;
  align-items: left;
  justify-content: space-between;
  padding: 30px;
  height: 100vh;
  padding: 30px;
  width: 100%;
  gap: 2em;
}

.app-overview {
  width: 85%;
  padding-bottom: 0;
}

.app-overview h1 {
  color: #fff;
  font-weight: 400;
  margin-bottom: 20px;
  letter-spacing: -0.03em;
  line-height: 1;
  text-align: left;
  align-content: top;
  font-family: "Black Han Sans", sans-serif;
  width: 100%; /* Add this line */
}

.app-overview .header1a {
  color: #fff;
  font-size: 13em;
  font-weight: 400;
  line-height: -1rem;
  margin: 0;
}

.app-overview .header1b {
  color: #fff;
  font-size: 3em;
  font-weight: 400;
}

.app-overview p {
  color: #fff;
  font-size: 1.25rem;
  font-weight: 300;
  margin-bottom: 20px;
  letter-spacing: 0.02em;
  line-height: 1.5;
  align-content: top;
  width: 100%;
}

.app-overview .app-copyright {
  font-size: 0.75rem;
  bottom: 0; /* Add this line */
  position: fixed;
  width: 100%; /* Add this line */
}

.app-container-overview {
  display: flex;
  flex-direction: row;
  align-items: left;
  justify-content: flex-start;
  gap: 2em;
  padding: 0px;
  height: 100vh;
}

.app-chatbutton {
  width: 8%;
  align-content: center;
  margin-right: 15px;
  position: relative; /* Add this line */
  border-left: 1px solid #fff;
}

.app-chatbutton-pointer {
  transform: rotate(-90deg);
  transform-origin: right top;
  font-size: 1.25em;
  letter-spacing: 0.02em;
  line-height: 3.5rem;
  font-weight: 300;
  color: white;
  white-space: nowrap; /* Add this line */
  position: absolute; /* Add this line */
  right: 85px; /* Add this line */
  bottom: 420px;
  font-family: "Black Han Sans", sans-serif;
}

p .material-symbols-outlined {
  font-size: 2em; /* Adjust size as needed */
  vertical-align: middle; /* Aligns the icon with the text */
  margin-right: 5px; /* Adds some space between the icon and the text */
}

.chatbox-container {
  width: 100%;
}

/* Styles for larger screens */
@media (max-width: 480px) {
  .chatbot-logo {
    width: 30%;
  }
}
